// > special-box
.special-box {
    min-height: 590px;
    padding: 25px 0 30px;
    background-color: $second-color;
    .story__meta {
        a,
        span,
        time {
            color: #000;
        }
    }
    &__heading {
        text-align: center;
        .main {
            @include font(24, 30, $normal);
            text-transform: uppercase;
        }
        .sub {
            >* {
                @include font(14, 26, $normal);
                display: inline-block;
                margin-left: 10px;
            }
        }
    }
    &__content {
        position: relative;
        padding-top: 20px;
        @include clearfix();
        .col-300 {
            float: right;
        }
    }
    .first-story {
        margin-bottom: 16px;
        .story {}
        .story__thumb {
            position: absolute;
            width: 800px;
            height: 450px;
            left: 0;
            top: 25px;
        }
        .story__heading {
            @include font(24, 30, $normal);
            margin-bottom: 5px;
            .cate {
                margin-top: 7px;
            }
        }
        .story__meta {
            margin-bottom: 10px;
            > * {
                &:after {
                    background-color: #000;
                }
            }
        }
        .story__summary {}
    }
    .more-stories {
        padding-top: 16px;
        .story {
            margin-bottom: 16px;
            @include last-y;
        }
        .story__thumb {
            width: 135px;
            height: 75px;
            @include runaround(left, 3, 16, 0, 0);
        }
        .story__heading {
            @include font(14, 18);
        }
    }
    &.theme-black {
        background-color: #2B3035;
        color: #fff;
        .story__meta {
            a,
            span,
            time {
                color: #fff;
            }
        }
        .special-box__heading {
            .main {}
            .sub {
                .is-active {
                    border-bottom: 1px solid #fff;
                }
            }
        }
        a {
            color: #fff;
        }
        .more-stories {
            border-top: 1px solid #fff;
        }
    }
}
