                background-size: ($sprite-w + px) ($sprite-h + px);
                background-position: 0 -1150px;
                background-repeat: repeat-x;
            }
            &:after {
                content: "";
                position: absolute;
                top: 18px;
                width: 100%;
                height: 65px;
                background-color: rgba(255, 255, 255, 1);
                z-index: 10;
            }
            span {
                z-index: 20;
                position: relative;
            }
            width: 100px;
            position: absolute;
            top: 30px;
            left: 0;
            padding-top: 25px;
