            display: block;
            float: left;
            padding: 0 10px;
        }
        .story:first-child {
            // margin-left: 0;
            padding-top: 0;
            .story__time {
                top: 10px;
                &:before {
                    display: none;
                }
            }
        }
        .story:before {
            position: absolute;
            top: 0%;
            left: 50px;
            content: '';
            width: 3px;
            height: 100%;
            background-color: #E0E0E0;
        }
    }
}


